export default {
	data() {
		return {
			user:'',
			userId:'',
			userName:'',
			password1:'',
			password2:''
		}
	},
	onLoad:function (parm) {
		if(parm&&parm.data)
		{
			this.user = JSON.parse(parm.data);
			console.log('parm.data='+parm.data);
			this.userId = this.user.id;
			this.userName = this.user.account;
		}
	},
	methods: {
		reSetPassword() {
			if(!this.userName)
			{
				uni.showToast({
					title: '会员账号不能为空!',
					duration: 2000,
					icon:'none'
				});
				return ;
			}
			if(this.password1.length==0 ||this.password2.length==0)
			{
				uni.showToast({
					title: '密码不能为空!',
					duration: 2000,
					icon:'none'
				});
				return ;
			}
			if(this.password1 != this.password2)
			{
				uni.showToast({
					title: '两次输入密码不一致!',
					duration: 2000,
					icon:'none'
				});
				return ;
			}
			this.bus.request({
				url: this.bus.url+'/agent/changeMemberPwd',
				method: 'GET',
				 header:this.bus.user.header,
				data: {
					agentId:this.bus.user.userId,
					// userId:this.userId,
					account:this.userName,
					password:this.password2},
				success: res => {
					res = res.data;
					if(res.status=='1')
					{
						this.bus.login.password = this.password3
						uni.showToast({
							title: '密码修改成功!',
							duration: 2000
						});
					} else{
						uni.showToast({
							title: res.errMsg,
							duration: 2000,
							icon:'none'
						});
					}
				},
				fail: () => {
					uni.showToast({
						title: '网络异常',
						duration: 2000,
						icon:'none'
					});
				},
				complete: () => {
					uni.hideLoading()
				}
			});
		}
	}
}
